    <div id="search_box">
      <label id="search_box_contents">
        <i class="fas fa-search"></i>
        <input type="text" placeholder="hollywood" name="search" id="search" bind:value="{searchWords}" />
      </label>
    </div>
  </div>
  <div id="img_container">
    <Gallery resultArray="{resultArray()}" />
  </div>
  <Footer />
</main>

